<template>
  <div class="ingredient">
    <h3>{{ info ? info.strIngredient : "Ingredient name" }}</h3>
    <div class="description">
      <div class="description__block-left">
        <div class="description__image">
          <img :src="thumb" alt="Ingredient image" />
        </div>
        <div class="description__stats">
          <div class="type">
            <p>Type:</p>
            <p>{{ info ? info.strType || "Google it!" : "Google it!" }}</p>
          </div>
          <div class="alco">
            <p>Alcohol:</p>
            <p>{{ info ? info.strAlcohol || "Google it!" : "Google it!" }}</p>
          </div>
          <div class="abv">
            <p>ABV:</p>
            <p>{{ info ? info.strABV || "Google it!" : "Google it!" }}</p>
          </div>
        </div>
      </div>

      <div class="description__block">
        {{ info ? info.strDescription || `If you want to learn more about ${info.strIngredient} just google it!` : "Google it!" }}
      </div>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  computed: {
    info() {
      return this.$store.getters.getIngrInfo;
    },
    thumb() {
      return this.info ? `https://www.thecocktaildb.com/images/ingredients/${this.info.strIngredient
        .split(" ")
        .map((s) => s.toLowerCase())
        .join(" ")}-Medium.png` 
        : null
      // return this.info.strIngredient.split(" ").map(s => s.toLowerCase()).join("_");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../scss/variables.scss";
@import "../scss/mixins.scss";
.ingredient {
  height: 100%;
  padding: 75px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  h3 {
    @include ingredient-title-font(orange);
    // font-family: "La Belle Aurore";
    font-size: 42px;
    font-weight: bold;
  }
  .description {
    display: flex;
    gap: 30px;
    .description__block-left {
      display: flex;
    .description__stats {
      padding: 15px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .type,
      .alco,
      .abv {
        display: flex;
        font-size: 20px;
        gap: 20px;
        :nth-child(1) {
          @include card-text-pink;
        }
        :last-child {
          @include card-text-blue;
        }
      }
    }
    }

    .description__block {
      @include card-text-blue;
      font-size: 20px;
      letter-spacing: 0.1rem;
    }
  }
}
</style>